import Vue from 'vue';
import refreshView from './Refresh.vue'
export default {
	components: {
		refreshView
	},
	name: 'waterFill',
	data() {
		return {
			waterList: [],
			errorImg: 'this.src="' + require('../static/img_photo_default.png') + '"'
		}
	},
	methods: {
		// 将数组拆分成可使用状态,减少代码
		groupCut(array, subGroupLength) {
			let index = 0;
			let newArray = [];
			while (index < array.length) {
				newArray.push(array.slice(index, index += subGroupLength));
			}
			return newArray;
		},
		handleLoad(arr) {
			// 如果arr存在,即在父组件刷新的值  若不存在就是下拉请求刷新
			if (arr) {
				let length = arr.length;
				this.waterList = this.groupCut(arr, length / 2)
			} else {
				console.log('写请求数据接口，将数据赋值给waterList')
			}
			uni.showToast({
				title: '刷新啦',
				icon: 'none'
			})
		},
		// 监听页面下拉刷新
		//触摸开始
		handletouchstart: function(e) {
			let that = this;
			that.$refs.refreshView.handletouchstart(e)
		},

		//触摸移动
		handletouchmove: function(e) {
			let that = this;
			that.$refs.refreshView.handletouchmove(e)
		},
		//触摸结束
		handletouchend: function(e) {
			let that = this;
			that.$refs.refreshView.handletouchend(e);
		},
		//获取下拉刷新的状态
		getState(state) {
			let that = this;
			if (state == 0) {
				that.handleLoad();
			}
		},
		// 查看图片详情
		previewImg(img, index) {
			let arr = [];
			arr.push(img);
			uni.previewImage({
				current: index,
				urls: arr
			})
		}
	},
}
